/* -------------------------------------------------------------------------- */
/*                                 CTA SECTION                            */
/* -------------------------------------------------------------------------- */
.section-cta {
  /* top / right / bottom / left */
  /* padding: 9.6rem 0 12.8rem 0; */
  /* top / horizontal spacing / bottom  */
  padding: 6.4rem 0 6.4rem 0;
}
.cta {
  display: grid;
  grid-template-columns: 2fr 1fr;
  /* background-color: #0ca678; */
  box-shadow: 0 2.4rem 4.8rem rgba(0, 0, 0, 0.2);
  border-radius: 11px;
  background-image: linear-gradient(to right bottom, #55c1a1, #0ca678);
  /* padding-right: 1rem; */
  overflow: hidden;
  justify-content: end;
  color: black;
}
.cta-text-box {
  padding: 2.4rem;
  color: #043224;
}

.cta .heading-secondary {
  color: inherit;
  /* color: #043224; */
  margin-bottom: 1.8rem;
}

.cta-text {
  font-size: 1.8rem;
  line-height: 1.8;
  margin-bottom: 1.8rem;
  font-weight: 400;
}
.cta-image-box {
  background-image: linear-gradient(
      to right bottom,
      rgba(85, 193, 161, 0.268),
      rgba(12, 166, 120, 0.271)
    ),
    url("../img/projects/sliding-aluminum.jpg");
  background-size: cover;
  /* background-repeat: no-repeat; */
  background-position: center;
  /* justify-self: end; */
}

.cta-form {
  border-radius: 1rem;
  .my-input {
    padding-left: 3px;
    &.input-round {
      border-radius: 5rem;
    }
    width: 100%;
    font-size: 1.8rem;
    font-family: inherit;
    color: inherit;
    border: none;
    background-color: #e7f6f2;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    height: 4.6rem;
    line-height: 4.6rem;
    font-family: "Gotham", sans-serif;
    font-size: 1.7rem;
  }
  .my-input:disabled {
    appearance: none;
    -webkit-appearance: none;
    opacity: 1;
  }
  .select-box {
    padding-left: 1rem;
    font-size: 1.8rem;
    font-family: inherit;
    color: inherit;
    border: none;
    background-color: #e7f6f2;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
    height: 4.6rem;
  }
  .country-code {
    border-radius: 0;
    border-radius: 0 !important;
    width: 4.8rem;
  }
}
.small {
  font-size: 1.2rem;
}

.cta .heading-secondary {
  margin-bottom: 1.2rem;
}

.cta *:focus {
  outline: none;
  /* outline: 4px dotted #0ca678;
  outline-offset: box; */
  /* outside element not inside */
  /* box-shadow: 0 0 0 0.5rem rgba(231, 246, 242, 0.5); */
}
.phone-pattern {
  font-size: 1.5rem;
  margin-top: 0.5rem;
  display: inline-block;
}

/* -------------------------------------------------------------------------- */
/*                               BELOW 800px /16px (Smaller tablet)                 */
/* -------------------------------------------------------------------------- */
@media (max-width: 50em) {
  .container {
    padding: 0 0.8rem;
  }
  .cta {
    grid-template-columns: 1fr;
  }
  .cta-text {
    margin-bottom: 0.5rem;
  }
  .cta-image-box {
    height: 32rem;
    grid-row: 1;
  }

  .note-title,
  .abs-title {
    text-align: left;
    font-size: 2.4rem;
  }

  .note-data {
    text-align: left;
    padding-bottom: 1.2rem;
    font-size: 1.8rem;
  }

  .section-cta {
    padding-bottom: 2.4rem;
  }
}
